.newletter {
    background-color: #d9d9a5;
    padding: 50px 0;
    color: #312f2f;
}

.newletter .right,
.newletter .left {
    padding-right: 50px;
}

.newletter h1 {
    color: #161615;
    font-size: 30px;
    font-weight: 500;
    margin-bottom: 15px;
}

h2 {
    font-size: 50px;
    padding: 10px;
}

.newletter .right {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.newletter input {
    width: 100%;
    padding: 20px;
    border: none;
    outline: none;
    height: 30px;
}

.newletter i {
    padding: 12px;
    color: #292927;
    background-color: #1d1b1b;
}

footer {
    background-color: #d9d9a5;
}

footer .container {
    display: grid;
    grid-template-columns: 2fr 1fr 1fr;
    grid-gap: 30px;
}

@media screen and (max-width: 768px) {
    footer .container {
        grid-template-columns: repeat(2, 1fr);
    }
}

footer .logo span {
    color: #262623;
    font-size: 14px;
}

footer .logo p {
    color: #1b1b19;
    margin: 30px 0 15px 0;
}

footer .logo .icon {
    background-color: #d9d9a5;
    color: rgb(51, 51, 51);
    margin-right: 10px;
}

footer h3 {
    margin-bottom: 40px;
    font-weight: 500;
}

footer ul li {
    margin-bottom: 20px;
}

footer .link li::before {
    content: "‚ü∂";
    color: #30302e;
    margin-right: 5px;
}

footer .items {
    margin-bottom: 30px;
}

footer .items img {
    width: 50px;
    height: 50px;
    border-radius: 5px;
    margin-right: 10px;
    object-fit: cover;
}

footer .items span,
footer .items i {
    font-size: 12px;
    color: #272724;
    margin-right: 5px;
    text-transform: capitalize;
}

footer h4 {
    font-weight: 400;
    margin-top: 5px;
}

footer .last ul li {
    display: flex;
    margin-bottom: 50px;
}

footer .last i {
    width: 60px;
    font-size: 20px;
    color: #1f1f1c;
}

.legal {
    text-align: center;
    padding: 50px 0;
    color: #d9d9a5;
    background-color: #282727;
}

.legal p {
    font-size: 15px;
}

.legal i {
    color: #d9d9a5;
}